  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0;
  padding-bottom: 40px;
}

.faq__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.faq__item {
  color: #102654;
  text-align: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  border: 1px solid rgba(16, 38, 84, 0.2);
  width: 260px;
  height: 54px;
  background: #ffffff;
}
@media (hover: hover) {
  .faq__item:hover:not(.focus-visible) {
    border: 1px solid var(--primary, #102654);
  }
}
.faq__item.focus-visible:active {
  color: #ffffff;
  background: var(--1, linear-gradient(151deg, #ed0233 0%, #003eb7 100%));
}
.faq__item:disabled {
  border-bottom: 2px solid #C8C9CC;
  color: #C8C9CC;
}

.tab__list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 27px;
  margin: 0;
}

.tab__item {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  position: relative;
}

.tab__header {
  margin: 0;
  padding-top: 32px;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  padding-right: 100px;
}

.tab__description {
  margin: 0;
  padding-top: 19px;
  font-size: 18px;
  font-weight: 300;
  padding-right: 171px;
}

.tab__header::before {
  content: "";
  position: absolute;
  width: 860px;
  border: 1px solid rgba(28, 51, 116, 0.2);
  top: 0;
  z-index: 2;
}

.tab__toggle {
  position: absolute;
  right: -19px;
  top: 24px;
}

.feedback__wrapper {
  padding: 120px 403px 120px 403px;
}

.feedback__title {
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  padding-bottom: 37px;
  margin: 0;
  text-align: center;
}

.swiper {
  width: 100%;
  height: 100%;
  margin: 20px auto;
}

.feedback__slide {
  background: #1C3374;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  padding: 62px;
  display: grid;
  grid-template-columns: max-content auto;
  gap: 28px;
}

.feedback__name {
  margin: 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
}

.feedback__description {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  margin: 0;
  line-height: 140%;
}

.feedback-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.wrapper__section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  padding: 0;
}

.custom__wrapper {
  padding: 56px 103px 70px 100px;
  color: #ffffff;
  background: #102654;
}

.custom__wrapper h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 800;
  margin: 0;
  padding-bottom: 22px;
  letter-spacing: 2.62px;
  line-height: 156%;
}

.custom__buttom {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  text-transform: uppercase;
  width: 409px;
  height: 52px;
  color: #ffffff;
  border: 2px solid #ffffff;
  background: #102654;
  margin-top: 30px;
}

.input {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  width: 192px;
  border: none;
  background: #102654;
  border-bottom: 1px solid #ffffff;
  margin-right: 25px;
}

::placeholder {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.contacts__wrapper {
  color: #102654;
  padding: 56px 2px 63px 122px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

.contacts__wrapper h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0;
}

.contacts__intro {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  position: relative;
  padding-top: 38px;
}

.contacts__intro::after {
  content: "";
  position: absolute;
  border: 1px solid #74819C;
  width: 102px;
  transform: rotate(90deg);
  top: 93px;
  right: 275px;
}

.contacts__header {
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  margin: 0;
  padding: 0;
}

.contacts__description {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

.footer__wrapper {
  background: #030A1A;
  padding-top: 41px;
  padding-bottom: 32px;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  padding-left: 7.75%;
  padding-right: 7.75%;
}
@media (max-width: 767px) {
  .footer__wrapper {
    grid-template-columns: 1fr 1fr;
    padding-left: 5.859375%;
    padding-right: 5.859375%;
    grid-auto-flow: column;
  }
}

.footer__logo {
  width: 5.5em;
  height: 2em;
  position: relative;
}
@media (max-width: 767px) {
  .footer__logo {
    width: 4.25em;
    height: 0.9375em;
  }
}

.footer__svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.footer-nav__list {
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 2.5em;
}
@media (max-width: 767px) {
  .footer-nav__list {
    display: block;
  }
}

.footer-nav__link {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 0.875em;
  font-weight: 400;
  line-height: normal;
  padding-top: 8px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .footer-nav__link {
    font-size: 1em;
  }
}

.footer__social-list {
  margin: 0;
  list-style-type: none;
  display: flex;
  gap: 10px;
  justify-content: center;
  padding-left: 2.875em;
}

.footer__social-item {
  background: #ffffff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
}
@media (hover: hover) {
  .footer__social-item:hover:not(.focus-visible) {
    background: #103AAE;
  }
}
.footer__social-item.focus-visible:active {
  background: #102654;
}

.footer__social-item:disabled {
  opacity: "0.3";
}

